  export default {
    name: 'split'
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .split {
    height: 16px;
    background: #f3f5f7;
    width: 100%;
    border-top: 1px solid rgba(7, 17, 27, 0.1);
    border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  }
</style>



